"use client";
import { useState } from "react";
import Image from "next/image";
import { Button } from "../ui/button";
import { updateStudent, deleteStudent } from "@/lib/api";
import type { Student } from "@/app/(dashboard)/school/student/page";
import { Pencil, Trash } from "lucide-react";

type Props = {
  students: Student[];
  setStudents: React.Dispatch<React.SetStateAction<Student[]>>;
};

export default function StudentTable({ students, setStudents }: Props) {
  const [editModalOpen, setEditModalOpen] = useState(false);
  const [deleteModalOpen, setDeleteModalOpen] = useState(false);
  const [selectedStudent, setSelectedStudent] = useState<Student | null>(null);
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const [editForm, setEditForm] = useState<any>({});

  const handleEdit = (student: Student) => {
    setSelectedStudent(student);
    setEditForm(student);
    setEditModalOpen(true);
  };

  const handleDelete = (student: Student) => {
    setSelectedStudent(student);
    setDeleteModalOpen(true);
  };

  const confirmEdit = async () => {
    if (!selectedStudent) return;
    const updated = await updateStudent(selectedStudent.id, editForm);
    setStudents((prev) =>
      prev.map((s) => (s.id === selectedStudent.id ? updated : s))
    );
    setEditModalOpen(false);
    setSelectedStudent(null);
  };

  const confirmDelete = async () => {
    if (!selectedStudent) return;
    await deleteStudent(selectedStudent.id);
    setStudents((prev) => prev.filter((s) => s.id !== selectedStudent.id));
    setDeleteModalOpen(false);
    setSelectedStudent(null);
  };

  return (
    <div className="space-y-4 relative">
      <div className="overflow-x-auto rounded-xl border border-gray-200 shadow-sm">
        <table className="min-w-full text-sm text-left">
          <thead className="bg-gray-50 text-gray-600 uppercase text-xs hidden sm:table-header-group">
            <tr>
              <th className="px-6 py-4">ឈ្មោះ</th>
              <th className="px-6 py-4">លេខទូរស័ព្ទមាតាបិតា</th>
              <th className="px-6 py-4">ភេទ</th>
              <th className="px-6 py-4">លេខអត្តសញ្ញាណបុគ្គល</th>
              <th className="px-6 py-4">បញ្ជារ</th>
            </tr>
          </thead>
          <tbody className="divide-y divide-gray-100 bg-white">
            {students.map((student) => (
              <tr key={student.id}>
                <td className="px-6 py-4 flex items-center gap-3">
                  {student.avatar ? (
                    <Image
                      src={student.avatar}
                      alt={student.name}
                      width={36}
                      height={36}
                      className="rounded-full object-cover"
                    />
                  ) : (
                    <div className="w-9 h-9 rounded-full bg-blue-100 flex items-center justify-center font-bold text-lg">
                      {student.name
                        .split(" ")
                        .map((n) => n[0])
                        .join("")
                        .toUpperCase()}
                    </div>
                  )}
                  <div className="text-sm font-medium">{student.name}</div>
                </td>
                <td className="px-6 py-4">{student.phone}</td>
                <td className="px-6 py-4">
                  <span
                    className={`px-3 py-1 rounded-full text-xs font-semibold ${
                      student.gender === "Male"
                        ? "bg-blue-100 text-blue-600"
                        : "bg-pink-100 text-pink-600"
                    }`}
                  >
                    {student.gender}
                  </span>
                </td>
                <td className="px-6 py-4">{student.parentPhone}</td>
                <td className="px-6 py-4 flex gap-2">
                  <button
                    onClick={() => handleEdit(student)}
                    className="text-blue-500 hover:text-blue-700"
                    title="Edit"
                  >
                    <Pencil size={18} />
                  </button>
                  <button
                    onClick={() => handleDelete(student)}
                    className="text-red-500 hover:text-red-700"
                    title="Delete"
                  >
                    <Trash size={18} />
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/* Edit Modal */}
      {editModalOpen && selectedStudent && (
        <div className="fixed inset-0 z-50 bg-black/50 flex items-center justify-center">
          <div className="bg-white p-6 rounded-lg shadow-md w-[90%] max-w-md">
            <div className="flex justify-between items-center mb-4">
              <h2 className="font-semibold text-lg">Edit Student</h2>
            </div>
            <input
              className="w-full px-4 py-2 border rounded-md mb-4"
              value={editForm.name}
              onChange={(e) => setEditForm({ ...editForm, name: e.target.value })}
            />
            {/* Add more fields as needed */}
            <Button onClick={confirmEdit} className="w-full bg-[#25388C] hover:bg-[#1e2e6d]">
              Save
            </Button>
          </div>
        </div>
      )}

      {/* Delete Modal */}
      {deleteModalOpen && selectedStudent && (
        <div className="fixed inset-0 z-50 bg-black/50 flex items-center justify-center">
          <div className="bg-white p-6 rounded-lg shadow-md w-[90%] max-w-sm text-center">
            <p className="font-semibold text-gray-800 mb-6">
              តើអ្នកចង់លុបបុគ្គលនេះមែនទេ?
            </p>
            <div className="flex justify-center gap-4">
              <Button
                variant="secondary"
                onClick={() => setDeleteModalOpen(false)}
              >
                បោះបង់
              </Button>
              <Button
                variant="destructive"
                onClick={confirmDelete}
              >
                លុបបុគ្គល
              </Button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}